class Elem {
	constructor(node) {
		this.node = node;
	}
	getWidth() {
		return this.node.offsetWidth;
	}
	setWidth(value) {
		let width;

		// Calculating width for values given in percent
		if (typeof value === 'string' && value.indexOf('pct') !== -1) {
			width = parseInt(value, 10) / 100 * this.node.parentElement.offsetWidth;
		}

		// The rest will be considered as value in pixels
		else {
			width = parseInt(value, 10);
		}
		if (!isNaN(width)) {
			this.node.style.width = Math.round(width) + 'px';
		}
	}
	getCoords() {
		const box = this.node.getBoundingClientRect();
		return {
			top: box.top + pageYOffset,
			left: box.left + pageXOffset
		};
	}
	getPosition() {
		const parent = new Elem(this.node.parentElement);
		return {
			top: (this.getCoords().top - parent.getCoords().top),
			left: (this.getCoords().left - parent.getCoords().left)
		};
	}
	setLeft(value) {
		this.node.style.left = value + 'px';
	}
}

class Handle extends Elem {
	snap(elem) {

		// Move handle to the right edge of element if handleSpot was given
		if (elem.handleSpot === 'right') {

			// "-2" is just empirically chosen value for better adjusting of handle element
			this.setLeft(elem.getPosition().left + elem.getWidth() - this.getWidth() / 2 - 2);
		}

		// Or to the left by default
		else {

			// "+2" - empirically chosen value too
			this.setLeft(elem.getPosition().left - this.getWidth() / 2 + 2);
		}
	}
}

function snapSlider(block, labelWidths) {

	const options = block.querySelectorAll('.field-slider__option');
	const track = new Elem(document.createElement('div'));
	const handle = new Handle(document.createElement('div'));
	const wrap = new Elem(document.createElement('div'));
	const labels = [];
	let down = false;
	let active;

	function setWidths(labelsArr, widths) {
		if (Array.isArray(widths)) {
			for (let i = 0; i < widths.length; i++) {
				labelsArr[i].setWidth(widths[i]);
			}
		}
		if (widths === 'equal') {
			for (let i = 0; i < labelsArr.length; i++) {
				labelsArr[i].setWidth(labelsArr[i].node.parentElement.offsetWidth / labelsArr.length);
			}
		}
	}

	function getActive(labelsArr) {
		for (let i = 0; i < labelsArr.length; i++) {
			if (labelsArr[i].associatedNode.selected) {
				return labelsArr[i];
			}
		}
	}

	function getClosest(event, labelsArr) {
		const closest = labelsArr.reduce(function (prev, curr) {

			const currShift = curr.handleSpot === 'right' ? curr.getWidth() : 0;
			const prevShift = prev.handleSpot === 'right' ? prev.getWidth() : 0;

			const currDist = curr.getCoords().left + currShift - event.pageX;
			const prevDist = prev.getCoords().left + prevShift - event.pageX;

			return Math.abs(currDist) < Math.abs(prevDist) ? curr : prev;
		});

		return closest;
	}

	block.querySelector('.field-slider__select').style.display = 'none';

	track.node.className = 'field-slider__track';
	handle.node.className = 'field-slider__handle';
	wrap.node.className = 'field-slider__wrap';
	track.node.appendChild(handle.node);
	block.appendChild(track.node);
	block.appendChild(wrap.node);

	// Create an array of labels, linked with options inside <select> element
	for (let i = 0; i < options.length; i++) {
		const label = new Elem(document.createElement('div'));

		// Bound label with relative <option> element
		label.associatedNode = options[i];

		// Last label in array will have different position for slider's handle
		label.handleSpot = (i === options.length - 1) ? 'right' : 'left';

		const text = document.createTextNode(options[i].value);
		label.node.appendChild(text);
		label.node.className = 'field-slider__label';
		wrap.node.appendChild(label.node);
		labels.push(label);
	}

	setWidths(labels, labelWidths);
	active = getActive(labels);
	handle.snap(active);

	track.node.addEventListener('mousedown', function (e) {
		down = true;
		const closest = getClosest(e, labels);
		handle.snap(closest);
		closest.associatedNode.selected = true;
		return false;
	});

	document.addEventListener('mousemove', function (e) {
		if (!down) {
			return;
		}
		const closest = getClosest(e, labels);
		handle.snap(closest);
		closest.associatedNode.selected = true;
	});

	document.addEventListener('mouseup', function () {
		down = false;
	});

	window.addEventListener('resize', function () {
		setWidths(labels, labelWidths);
		active = getActive(labels);
		handle.snap(active);
	});
}

const block = document.querySelector('.field-slider');
snapSlider(block, ['18pct', '27pct', '23pct', '26pct']);



